import React from 'react';
import { graphql } from 'gatsby';
import FadeLink from '../components/common/fadeLink';
import formatLink from '../components/common/formatLink';

const About = ({ data }) => {
  let disciplines = [];

  // for(const { entryDisciplines } of data.craft.entries){
  //   for(const { title } of entryDisciplines){
  //     let checkDiscipline = title.toLowerCase();

  //     if(disciplines.indexOf(checkDiscipline) === -1){
  //       disciplines.push(checkDiscipline);
  //     }
  //   };
  // };

  // disciplines is currently set here instead to prevent accidental additions in CMS
  disciplines = ['brand & identity', 'film & photography', 'spatial', 'digital'];

  const paragraphs = data.sanityAbout.paragraph.split("\n");

  return (
    <section className="about -dark-theme">
      <div className="row about__content">
        <div className="col__7 col__s-12">
          {paragraphs.map((text, i) => {
            if(text.length > 0){
              return <h2 key={"para-"+i} className="about__text">{text}</h2>;
            } else return null;
          })}
          <FadeLink to="/contact">
            <p className="about__contactLink fontSize-h3">Get in touch</p>
          </FadeLink>
        </div>
        <div className="col__2 col__s-0"></div>
        <div className="col__3 col__s-12">
          <div className="about__offerings">
            <h3 className="about__offerings-title fontSize-h3">Our offerings</h3>

            {disciplines.map(discipline => (
              <FadeLink key={discipline} to={'/work/' + formatLink(discipline)} key={discipline}>
                <p className="about__offerings-discipline fontSize-h3">{discipline}</p>
              </FadeLink>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export const query = graphql`
  {
    sanityAbout{
      paragraph
    }
    allSanityProject{
      nodes{
        disciplines
      }
    }
  }
`;

export default About;